.preloader {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 100;
  background-color: #1a1a1a;
}

.preloader .pulse {
  position: relative;
  left: 50%;
  top: 50vh;
  width: 80px;
  margin-left: -40px;
  margin-top: -40px;
}

.preloader .pulse::before,
.preloader .pulse::after {
  content: '';
  border: 5px solid #323232;
  width: 80px;
  height: 80px;
  border-radius: 500px;
  position: absolute;
}

.preloader .pulse::before {
  animation: pulse-outer .8s ease-in infinite;
}

.preloader .pulse::after {
  animation: pulse-inner .8s linear infinite;
}

@keyframes pulse-outer {
  0% {
    opacity: 1;
    filter: alpha(opacity=100);
  }

  50% {
    opacity: 0.5;
    filter: alpha(opacity=50);
  }

  100% {
    opacity: 0;
    filter: alpha(opacity=0);
  }
}

@keyframes pulse-inner {
  0% {
    opacity: 0;
    filter: alpha(opacity=0);
    transform: scale(0);
  }

  100% {
    opacity: 1;
    filter: alpha(opacity=100);
    transform: scale(1);
  }
}

