@import "vars";

/*==========  Desktop First  ==========*/

/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px) {
	.welcome {
		.h1 {
			font-size: 64px; } }
	.header-contacts {
		font-size: 14px; }
	.carousel-services-item {
		flex-direction: column; }


	.s {
		&-king, &-review, &-partners {
			padding: 10% 0; }
		&-king {
			.h2 {
				margin-top: 0; } }
		&-call {
			&::before {
				background-size: cover; } } } }

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {
	/**/
	.welcome {
		.h1 {
			font-size: 48px; } }
	.s-call {
		&::before {
			width: 100%; }
		.callback {
			padding: 12% 0; } }

	.footer-addr {
		margin-bottom: 15px;
		.footer-phone {
			font-size: 24px;
			padding-top: 0;
			.fas {
				top: 9px; } } } }


/* Small Devices, Tablets */
@media only screen and (max-width : 768px) {
	/**/
	.button {
		padding: 12px 36px; }

	.quotes {
		display: none; }

	.partners {
		.owl-nav {
			> div {
				&.owl-prev {
					left: 0; }
				&.owl-next {
					right: 0; } } } }

	.s-partners {
		.h2 {
			margin: 0; } }

	.footer-column-first {
		padding-left: 20px; }

	.footer-social {
		.soc li {
			a {} }
		margin-top: 0; }

	.footer-menu {
		padding-left: 20px; } }

/* Extra Small Devices, Phones */
@media only screen and (max-width : 576px) {
	.welcome {
		.h1 {
			font-size: 28px;
			margin-top: 5px; }
		p {
			span {
				font-size: 9px; } }
		strong {
			margin-top: 14px;
			font-size: 14px; } }
	.header-composition {
		p {
			font-size: 14px; } } }

/* Custom, iPhone Retina */
@media only screen and (max-width : 320px) {
	/**/
	.welcome {
		p {
			span {
				font-size: 8px; } } } }

